@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaina+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
/* Import Google Font */

body{
  font-family: 'Baloo Bhaina 2', cursive;
  background-color: white;
}

pre{
  color: white;
  background-color: #050b1f; 
}

#italize{
  font-style: italic;
  font-size: 15px;
}
.container-fluid{
  width: 60%;
  margin: 0 auto;
  margin-top: 100px;
}

#center-page{
  text-align: center;
  background-color: #0D659D;
}
.container-fluid h1 {
  color: #6BCAE2;
  position: relative;
  width: 23rem;
}

.container-fluid h1 ::after{
  position: absolute;
  bottom: 0;
  right: 12px;
  width: 67px;
  height: 2px;
  background-color: #6BCAE2;
}

.icon_accordion{
  width: 100%;
  padding: 0 5px;
  border: 2px solid #0D659D;
  cursor: pointer;
  border-radius: 50px;
  display: flex;
  margin: 10px 0;
  align-items: center;
}

.icon_accordion .icon{
  margin: 0 10px 0 0;
  width: 30px;
  height: 30px;
  background: #0D659D;
  border-radius: 50%;
  float: left;
  transition: all .5s ease-in;
}

.icon_accordion h5{
  font-size: 15px;
  margin: 0;
  padding: 3px 0 0 0;
  font-weight: normal;
  color: #1f5c9a;
  transition: all .5s ease-in;
}

.active .icon{
  background: #fff;
  z-index: 1;
}

.active {
  background-color: #0D659D;
  color: #fff;
}

.active h5{
  color: #fff;
}

.panel{
  padding: 0 18px;
  border-left: 1px solid #0D659D;
  margin-left: 25px;
  font-size: 14px;
  text-align: justify;
  overflow: hidden;
  max-height: 0px;
  transition: all .5s ease-in;
}
/* Nav bar general layout */

nav {
  position: fixed;
  z-index: 2;
  background-color: rgb(127, 255, 212);
  display: block;
  width: 100%;
  background-image: linear-gradient(to right, rgb(127, 255, 212), #c7d8e7);
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
}
/*Make the scrolling smooth*/
html {
  scroll-behavior: smooth;
}

.topnav {
  border-bottom: 1px solid #c7d8e7;
  height: 50px;
}

.topnav a {
  top: 0;
  float: right;
  padding: 5px 5px 5px 5px;
  margin: 13px 5px 5px 5px;
  text-decoration: none;
  color: black;
}
.topnav a:hover {
  border-radius: 10px;
  color: #000000;
  background-color: #7fffd4;
  text-decoration: none;
}

.bottomnav {
  border-top: 1px solid #c7d8e7;
  background-image: linear-gradient(to left, rgb(127, 255, 212), #c7d8e7);
  height: 50px;
  position: fixed;
  left: 0;
  bottom: 0;
}
.bottomnav a {
  bottom: 0;
  float: left;
  padding: 5px 5px 5px 5px;
  margin: 13px 5px 5px 5px;
  text-decoration: none;
  color: black;
}

.bottomnav a:hover {
  border-radius: 10px;
  color: #000000;
  background-color: #7fffd4;
}
.signin a {
  border-style: outset;
  text-align: center;
  text-decoration: none;
  padding: 5px 30px 5px 30px;
  margin: 10px 5px 5px 5px;
  border-radius: 10px;
  cursor: pointer;
}
.signin a:hover {
  background-color: #7fffd4;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
  color: black;
}

.logo {
  font-family: "Lobster", cursive;
  font-size: 20px;
  margin: 0 30px;
  padding: 11px 0;
  float: left;
}

.logo a {
  padding: 0;
  margin: 0;
  color: #0d659d;
}

.logo a:hover {
  color: #000;
}

#myBtn {
  /*    smooth scroll to top instead of jump*/
  scroll-behavior: smooth;
  display: none;
  position: fixed;
  text-align: center;
  width: 60px;
  height: 20px;
  bottom: 45px;
  right: 30px;
  z-index: 99;
  font-size: 15px;
  border: ridge;
  outline: none;
  background-color: #7fffd4;
  color: #000000;
  cursor: pointer;
  padding: 5px 15px 20px 15px;
  border-radius: 10px;
  border: inset;
}
/*Username dropdown list css*/
.topnav .navbar-dropdown a {
  margin: 7px 25px 10px 75px;
  padding: 7.5px 0px 7.5px 0px;
  background-color: transparent;
}
.topnav .navbar-dropdown-elements {
  overflow: hidden;
  max-height: 0;
  position: absolute;
  right: 1%;
  transition: max-height 0.7s ease;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  padding: 35px 0px 0px;
  margin: 5px 0px 0px 0px;
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.topnav .navbar-dropdown-elements a {
  display: block;
  float: none;
  text-decoration: none;
  color: black;
  padding: 10px 5px 0px;
  text-align: right;
  margin: 5px;
}

.topnav .navbar-dropdown:hover .navbar-dropdown-elements {
  max-height: 250px;
}

